.section {
   &__documentation {
      background-image: url(..//img/documentation-bg.webp);
      padding: 50px 0 460px;
   }
}
.documentation {
   position: relative;
   &__header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 50px;
      @media (max-width: 768px){
         flex-direction: column;
         margin-bottom: 30px;
      }
   }
   &__title {
      font-weight: 600;
      font-size: 40px;
      line-height: 1.3;
      color: #ffffff;
      margin-bottom: 10px;
      @media (max-width: 992px){
         font-size: 32px;
      }
      @media (max-width: 480px){
         font-size: 26px;
      }
   }
   &__text {
      color: #ffffff;
      @media (max-width: 768px){
         margin-bottom: 20px;
      }
   }
   .swiper__buttons {
      display: flex;
      align-items: center;
   }
   .swiper__btn {
      width: 50px;
      height: 50px;
      border: 1px solid #ffffff;
      display: flex;
      justify-content: center;
      align-items: center;
      transition: all 0.3s ease;
      border-radius: 50%;
      svg path {
         transition: all 0.3s ease;
      }
      &:hover {
         background: #fff;
         path {
            stroke: #2855a1;
         }
      }
   }
   .swiper-prev {
      margin-right: 10px;
      svg {
         transform: rotate(90deg);
      }
   }
   .swiper-next svg {
      transform: rotate(-90deg);
   }
   &__swiper {
      position: absolute;
      right: -50%;
      left: 0;
      @media (max-width: 480px){
         right: 0;
      }
      .slide {
         width: 470px;
         @media (max-width: 480px){
            width: 100%;
         }
         &__img {
            width: 100%;
            height: auto;
            margin-bottom: 20px;
            img {
               width: 100%;
               height: auto;
            }
         }
         &__content {
            display: flex;
            align-items: flex-start;
         }
         &__number {
            font-weight: 600;
            font-size: 32px;
            line-height: 1.2;
            text-transform: uppercase;
            color: #ffffff;
            margin-right: 30px;
            @media (max-width: 480px){
               margin-right: 15px;
            }
         }
         &__text {
            font-weight: 500;
         }
      }
   }
}
