header.home {
   background-image: url(..//img/header-bg.webp);
   background-position: center center;
   nav{
      background: none;
   }
}
nav{
   background: #2855a1;
}
.nav {
   display: flex;
   justify-content: space-between;
   align-items: center;
   flex-wrap: wrap;
   padding: 20px 0;
   @media (max-width: 768px) {
      flex-wrap: nowrap;
   }
   &__logo {
      @media (max-width: 400px) {
         width: 120px;
      }
      img {
         width: 100%;
         height: auto;
      }
   }
   &__menu {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      width: 100%;
      flex: 0 1 100%;
      @media (max-width: 768px) {
         display: none;
         &.active {
            display: flex;
            position: fixed;
            left: 0;
            top: 0;
            bottom: 0;
            right: 0;
            z-index: 99;
            background: #2855a1;
            justify-content: flex-start;
            flex-direction: column;
            align-items: center;
            flex-wrap: nowrap;
            padding: 100px 20px;
            overflow-y: auto;
            &::-webkit-scrollbar {
               display: none;
            }
         }
      }
   }
   &__contact {
      display: flex;
      align-items: center;
      @media (max-width: 768px) {
         margin-left: auto;
      }
      &.mobile {
         display: none;
         @media (max-width: 600px) {
            display: flex;
            margin-right: auto;
            .nav__text,
            .select {
               display: flex;
               flex-direction: column;
            }
            .nav__btn {
               margin-right: 20px !important;
               &:hover {
                  color: #ffffff !important;
               }
            }
         }
         @media (max-width: 450px) {
            margin-right: 0;
            width: 100%;
            flex-wrap: wrap;
            .nav__text {
               margin-bottom: 10px;
            }
         }
      }
   }
   &__text {
      color: #ffffff;
      margin-right: 50px;
      transition: all 0.3s ease;
      @media (max-width: 768px) {
         margin-right: 20px;
      }
      @media (max-width: 600px) {
         display: none;
      }
      &:hover {
         opacity: 0.7;
         transition: all 0.3s ease;
      }
   }
   &__btn {
      padding: 14px 25px;
      background: none;
      border: 1px solid rgba(255, 255, 255, 0.2);
      border-radius: 37px;
      margin-right: 20px;
      transition: all 0.3s ease;
      @media (max-width: 600px) {
         display: flex;
      }
      @media (max-width: 450px) {
         padding: 10px 20px;
         margin-right: 10px;
      }
      @media (max-width: 350px){
         display: none;
      }
      &:hover {
         transition: all 0.3s ease;
         opacity: 1;
         background: rgba(255, 255, 255, 0.2);
      }
   }
   .select {
      position: relative;
      @media (max-width: 600px) {
         display: none;
      }
      select {
         display: none;
      }
      &-selected {
         width: 45px;
         height: 45px;
         border-radius: 50%;
         border: 1px solid rgba(255, 255, 255, 0.2);
         display: flex;
         justify-content: center;
         align-items: center;
      }
      div,
      &-selected {
         cursor: pointer;
         user-select: none;
      }
      &-items {
         position: absolute;
         top: calc(100% + 10px);
         left: 0;
         right: 0;
         z-index: 99 !important;
         border: 1px solid rgba(255, 255, 255, 0.2);
         padding: 10px;
         border-radius: 10px;
         opacity: 1;
         visibility: visible;
         transform: translate(0, 0%);
         transition: all 0.3s ease;
         div {
            padding: 2px 0;
         }
      }
      &-hide {
         opacity: 0;
         visibility: hidden;
         transform: translate(0, -50%);
         transition: all 0.3s ease;
      }
   }
   &__list {
      display: flex;
      justify-content: space-between;
      align-items: center;
      list-style: none;
      width: 100%;
      margin-top: 30px;
      @media (max-width: 992px) {
         margin-top: 20px;
      }
      @media (max-width: 768px) {
         flex-direction: column;
         justify-content: flex-start;
         align-items: flex-start;
         margin-top: 20px;
      }
      li {
         position: relative;
         width: 100%;
         margin-right: 64px;
         padding-top: 16px;
         &:last-child {
            margin-right: 0;
         }
         &::after,
         &::before {
            content: "";
            position: absolute;
            opacity: 0.2;
            top: 0;
            left: 0;
            right: 0;
            width: 100%;
            height: 1px;
            background: #ffffff;
         }
         &::before {
            display: none;
         }
         @media (max-width: 992px) {
            margin-right: 20px;
            padding-top: 10px;
         }
         @media (max-width: 768px) {
            margin-right: 0;
            padding-bottom: 10px;
            &:last-child::before {
               bottom: 0 !important;
               top: auto;
               display: block;
            }
         }
      }
   }
   &__list-link {
      color: #ffffff;
   }
   .burger {
      display: none;
      @media (max-width: 768px) {
         width: 26px;
         height: 20px;
         display: block;
         position: relative;
         z-index: 100;
         &::after,
         &::before,
         span {
            content: "";
            position: absolute;
            width: 100%;
            height: 2px;
            left: 0;
            right: 0;
            background-color: #ffffff;
            transition: 0.3s ease 0s;
            transform-origin: right;
         }
         &::before {
            top: 0px;
         }
         &::after {
            bottom: 0px;
         }
         span {
            top: 50%;
            transform: translate(0, -50%);
         }
         &.active {
            &::before {
               transform: rotate(-45deg);
               transition: 0.3s ease 0s;
            }
            &::after {
               transform: rotate(45deg);
               transition: 0.3s ease 0s;
            }
            span {
               transform: translate(0, -50%) scaleX(0);
               transition: 0.3s ease 0s;
            }
         }
      }
   }
   .dropdown {
      position: relative;
      &.show {
         .dropdown__body {
            opacity: 1;
            visibility: visible;
            transform: translate(-50%, 0);
            transition: all .3s ease;
            height: auto;
            @media (max-width: 768px){
               transition: none;
            }
         }
         .dropdown__header img {
            transform: rotate(180deg);
            transition: all .3s ease;
         }
      }
      &__header {
         cursor: pointer;
         display: flex;
         align-items: center;
         justify-content: space-between;
         img{
            transition: all .3s ease;
         }
      }
      &__body {
         opacity: 0;
         visibility: hidden;
         transform: translate(-50%, -10%);
         list-style: none;
         width: calc(100% + 20px);
         position: absolute;
         left: 50%;
         top: calc(100% + 10px);
         z-index: 99;
         background: #2855a1;
         padding: 0 10px;
         transition: all .3s ease;
         @media (max-width: 768px){
            position: relative;
            top: 10px;
            height: 0;
            transition: none;
         }
         li {
            padding: 10px 0 !important;
            &::after,
            &::before {
               content: "";
               position: absolute;
               opacity: 0.2;
               top: 0;
               left: 0;
               right: 0;
               width: 100%;
               height: 1px;
               background: #ffffff;
            }
            &::before {
               display: none;
            }
            &:last-child::before {
               display: block;
               bottom: 0;
               top: auto;
            }
         }
      }
   }
}
