.section {
   &__document {
      padding: 100px 0;
      &-drill{
         .document{
            &__text{
               padding: 0 !important;
               border: none !important;
            }
            &__img{
               display: flex;
               width: 100%;
               @media (max-width: 480px){
                  flex-direction: column;
               }
               img{
                  width: 100%;
                  max-width: 270px;
                  margin-right: 30px;
                  @media (max-width: 1183px){
                     max-width: 200px;
                  }
                  @media (max-width: 992px){
                     max-width: 50%;
                  }
                  @media (max-width: 480px){
                     max-width: 100%;
                     margin-right: 0;
                     margin-bottom: 15px;
                     &:last-child{
                        margin-bottom: 0;
                     }
                  }
                  &:last-child{
                     margin-right: 0;
                  }
               }
            }
         }
      }
      &-services{
         .document{
            &__img{
               img{
                  max-width: 100%;
               }
            }
         }
      }
      &-research{
         padding: 0 0 100px !important;
      }
   }
}
.document {
   display: flex;
   align-items: center;
   justify-content: space-between;
   @media (max-width: 992px){
      flex-direction: column-reverse;
      align-items: flex-start;
   }
   &__img {
      width: auto;
      height: auto;
      margin-right: 30px;
      @media (max-width: 992px){
         margin-right: 0;
         width: 100%;
      }
      img {
         width: 100%;
         height: auto;
      }
   }
   &__content {
      max-width: 670px;
      width: 100%;
      @media (max-width: 1183px){
         max-width: 500px;
      }
      @media (max-width: 992px){
         max-width: 100%;
         margin-bottom: 40px;
      }
   }
   &__title {
      font-weight: 700;
      font-size: 40px;
      line-height: 1.5;
      color: #222a3c;
      margin-bottom: 30px;
   }
   &__text {
      font-weight: 500 !important;
      color: #444a58;
      padding-bottom: 25px;
      margin-bottom: 25px;
      border-bottom: 1px solid #E8E8E8;
      &:nth-child(2){
         border-top: 1px solid #E8E8E8;
         padding-top: 25px;
      }
      &:last-child{
         border: none;
         padding: 0;
         margin: 0;
      }
      @media (max-width: 480px){
         padding-bottom: 15px;
         margin-bottom: 15px;
         &:nth-child(2){
            padding-top: 25px;
         }
      }
   }
   &__text-bold {
      font-weight: 600;
   }
}
