.section {
   &__news {
      padding: 100px 0 495px;
      &-news {
         padding: 50px 0 130px;
         .news {
            &__header {
               align-items: flex-start;
            }
         }
      }
   }
}
.news {
   position: relative;
   &__header {
      margin-bottom: 50px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      @media (max-width: 480px) {
         flex-direction: column;
         margin-bottom: 30px;
      }
   }
   &__title {
      font-weight: 600 !important;
      color: #222a3c;
      @media (max-width: 480px) {
         margin-bottom: 20px;
      }
   }
   .swiper__buttons {
      display: flex;
      align-items: center;
   }
   .swiper__btn {
      width: 50px;
      height: 50px;
      background: #f8f8f8;
      display: flex;
      justify-content: center;
      align-items: center;
      transition: all 0.3s ease;
      border-radius: 50%;
      svg path {
         transition: all 0.3s ease;
      }
      &:hover {
         background: #2855a1;
         path {
            stroke: #fff;
         }
      }
   }
   .swiper-prev {
      margin-right: 10px;
      svg {
         transform: rotate(90deg);
      }
   }
   .swiper-next svg {
      transform: rotate(-90deg);
   }
   &__swiper {
      position: absolute;
      right: -50%;
      left: 0;
   }
   .slide {
      width: 270px;
      height: 340px;
      padding: 20px 20px 63px;
      background: #f8f8f8;
      position: relative;
      z-index: 0;
      &__bg {
         width: 100%;
         height: 100%;
         object-fit: cover;
         position: absolute;
         left: 0;
         top: 0;
         bottom: 0;
         right: 0;
         z-index: -1;
         opacity: 0;
         transition: all 0.3s ease;
      }
      &:hover {
         .slide__bg {
            opacity: 1;
            transition: all 0.3s ease;
         }
         .slide__text,
         .slide__number,
         .slide__date {
            color: #ffffff;
            transition: all 0.3s ease;
         }
      }
      &__text {
         font-weight: 500 !important;
         color: #222a3c;
         padding-bottom: 20px;
         border-bottom: 1px solid #e8e8e8;
         transition: all 0.3s ease;
      }
      &__content {
         display: flex;
         justify-content: space-between;
         align-items: center;
         position: absolute;
         bottom: 20px;
         right: 20px;
         left: 20px;
      }
      &__number {
         font-weight: 600 !important;
         font-feature-settings: "pnum" on, "lnum" on;
         color: #2855a1;
         transition: all 0.3s ease;
      }
      &__date {
         transition: all 0.3s ease;
      }
   }
   &__items {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      gap: 30px;
      margin-bottom: 50px;
      @media (max-width: 992px) {
         grid-template-columns: repeat(3, 1fr);
      }
      @media (max-width: 768px) {
         grid-template-columns: repeat(2, 1fr);
      }
      @media (max-width: 480px) {
         grid-template-columns: repeat(1, 1fr);
      }
   }
   &__item {
      width: 100% !important;
      height: auto !important;
      min-height: 340px;
   }
   &__pagination {
      list-style: none;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      flex-wrap: wrap;
   }
   &__pagination-item {
      width: 40px;
      height: 40px;
      background: #ffffff;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      transition: all 0.3s ease;
      margin-right: 5px;
      margin-bottom: 5px;
      a {
         color: #222a3c;
         transition: all 0.3s ease;
      }
      &.active {
         background: #2855a1;
         a {
            color: #ffffff;
         }
      }
      &:hover {
         border: 1px solid #2855a1;
         color: #2855a1;
         transition: all 0.3s ease;
      }
   }
   &__pagination-next {
      width: auto;
      height: auto;
      border-radius: 0;
      margin-left: 20px;
      a {
         color: #2855a1;
         margin-right: 16px;
      }
      svg{
         transform: rotate(-90deg);
      }
      &:hover {
         border: none;
      }
   }
}
