.section {
   &__about {
      padding: 225px 0 50px;
      background-image: url(..//img/about-bg.webp);
      background-position: bottom center;
      @media (max-width: 768px) {
         padding: 150px 0 50px;
         background-position: bottom bottom;
      }
      &-about {
         background: none !important;
         padding: 50px 0 100px !important;
         .about{
            display: flex;
            align-items: flex-start;
            flex-wrap: wrap;
            &__text{
               margin-left: 0;
               max-width: 100%;
               margin-bottom: 30px;
               &:first-child, &:nth-child(2), &:nth-child(3), &:nth-child(4){
                  flex: 0 1 calc(50% - 30px);
                  margin-right: 30px;
                  @media (max-width: 992px){
                     flex: 0 1 100%;
                     margin-right: 0;
                  }
               }
            }
         }
      }
   }
}
.about {
   &__title {
      font-weight: 500 !important;
      color: #222a3c;
      margin-bottom: 100px;
      max-width: 620px;
      width: 100%;
      @media (max-width: 600px) {
         margin-bottom: 50px;
      }
      @media (max-width: 480px) {
         font-size: 20px;
      }
      @media (max-width: 400px) {
         margin-bottom: 20px;
      }
   }
   &__text {
      color: #444a58;
      max-width: 457px;
      width: 100%;
      margin-bottom: 130px;
      margin-left: 200px;
      @media (max-width: 992px) {
         margin-left: 100px;
         margin-bottom: 50px;
      }
      @media (max-width: 600px) {
         margin-left: 0;
      }
      @media (max-width: 400px) {
         font-size: 14px;
      }
   }
   &__btn {
      font-weight: 600 !important;
      color: #2855a1;
      padding: 15px 40px;
      border: 1px solid #2855a1;
      margin-left: 200px;
      transition: all .3s ease;
      &:hover{
         background: #2855a1;
         transition: all .3s ease;
         color: #fff !important;
      }
      @media (max-width: 992px) {
         margin-left: 100px;
      }
      @media (max-width: 600px) {
         margin-left: 0;
      }
   }
   &__scroll {
      margin-top: 130px;
      width: 50px;
      height: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      border: 1px solid #2855a1;
      transition: all .3s ease;
      &:hover{
         transition: all .3s ease;
         background: #fff;
      }
      @media (max-width: 992px) {
         margin-top: 50px;
      }
   }
}
