.section {
   &__project {
      padding: 50px 0 100px;
      &-research {
         padding: 50px 0 !important;
         .project {
            &__content {
               margin-bottom: 50px;
            }
            &__items {
               margin-bottom: 30px;
            }
         }
      }
      &-nature {
         .project {
            &__images {
               display: flex;
               align-items: center;
               margin-top: 50px;
               @media (max-width: 768px){
                  flex-wrap: wrap;
               }
            }
            &__items{
               margin-bottom: 30px;
            }
            &__text:nth-child(4){
               margin-bottom: 20px;
            }
            &__img{
               margin-right: 30px;
               @media (max-width: 768px){
                  flex: 0 1 calc(50% - 30px);
               }
               @media (max-width: 480px){
                  margin-right: 0;
                  flex: 0 1 100%;
                  margin-bottom: 20px;
               }
               &:last-child{
                  margin-right: 0;
               }
               img{
                  width: 100%;
               }
            }
         }
      }
   }
}
.project {
   &__title {
      color: #222a3c;
      margin-bottom: 20px;
   }
   &__content {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      margin-bottom: 70px;
      @media (max-width: 768px) {
         flex-direction: column;
      }
      .project__text {
         margin-right: 30px;
         @media (max-width: 768px) {
            margin-right: 0;
            margin-bottom: 20px;
            &:last-child {
               margin-bottom: 0;
            }
         }
         &:last-child {
            margin-right: 0;
         }
      }
   }
   &__text {
      color: #444a58;
   }
   &__items {
      margin-top: 10px;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 30px;
      @media (max-width: 992px) {
         grid-template-columns: repeat(2, 1fr);
      }
      @media (max-width: 600px) {
         gap: 15px;
      }
      @media (max-width: 480px) {
         grid-template-columns: repeat(1, 1fr);
      }
   }
   &__item {
      padding: 30px;
      background: #f8f8f8;
      width: 100%;
      @media (max-width: 600px) {
         padding: 15px;
      }
      .project__text {
         font-weight: 500;
      }
   }
   &__item-number {
      font-weight: 500 !important;
      text-transform: uppercase;
      color: #2855a1;
      margin-bottom: 6px;
   }
   &__subtitle {
      color: #444a58;
      margin-top: 30px;
   }
}
