.section {
   &__features {
      padding: 102px 0 100px;
      background-color: #394854;
      background-image: url(..//img/features-bg.webp);
   }
}
.features {
   position: relative;
   &__title {
      font-weight: 500 !important;
      color: #ffffff;
      border-bottom: 1px solid #ffffff21;
      padding-bottom: 20px;
      margin-bottom: 60px;
   }
   &__content {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      flex-wrap: wrap;
      @media (max-width: 992px){
         flex-direction: column;
         flex-wrap: nowrap;
         justify-content: flex-start;
      }
   }
   &__text {
      flex: 0 1 50%;
      width: 100%;
      color: #ffffff;
      @media (max-width: 480px){
         font-size: 16px;
      }
      &:first-child {
         order: 1;
         margin-bottom: 195px;
         max-width: 412px;
      }
      &:nth-child(2) {
         order: 3;
         margin-bottom: 204px;
         max-width: 248px;
      }
      &:nth-child(3) {
         order: 2;
      }
      &:nth-child(4) {
         order: 5;
         margin-top: -80px;
      }
      @media (max-width: 992px){
         flex: unset !important;
         order: unset !important;
         margin-bottom: 50px !important;
         margin-top: 0 !important;
         max-width: 100% !important;
      }
   }
   &__item {
      order: 4;
      @media (max-width: 992px){
         position: relative;
         order: unset;
         margin: 100px auto 0;
      }
   }
   &__img {
      width: 100%;
      min-width: 570px;
      height: 386px;
      position: relative;
      @media (max-width: 1183px) {
         min-width: 420px;
      }
      @media (max-width: 992px){
         min-width: 570px;
      }
      @media (max-width: 600px){
         min-width: unset !important;
         max-width: 100%;
      }
      .features__text {
         text-align: right;
         flex: unset;
         max-width: 326px;
         width: 100%;
         z-index: 99;
         position: absolute;
         bottom: 30px;
         right: 30px;
         @media (max-width: 600px){
            right: 10px;
            bottom: 20px;
            padding-left: 20px;
            margin-bottom: 0 !important;
         }
      }
      img {
         width: 100%;
         height: 100%;
         position: absolute;
         left: 0;
         top: 0;
         bottom: 0;
         right: 0;
         object-fit: cover;
         z-index: 0;
         @media (max-width: 600px){
            position: relative;
         }
      }
   }
   &__country {
      position: absolute;
      left: 50%;
      top: calc(50% + 10px);
      transform: translate(-50%, -50%);
      z-index: 99;
      @media (max-width: 1183px) {
         width: 500px;
         left: 55%;
         img{
            width: 100%;
            height: auto;
         }
      }
      @media (max-width: 992px){
         left: 0;
         top: 20px;
      }
      @media (max-width: 768px){
         left: 50%;
         top: 0;
      }
      @media (max-width: 600px){
         width: 100%;
      }
      @media (max-width: 450px){
         top: 20px;
      }
   }
   &__country-circle {
      background: #2855a1;
      opacity: 0.5;
      width: 301px;
      height: 301px;
      border-radius: 50%;
      position: absolute;
      left: -50%;
      top: -50%;
      transform: translate(45%, 13.5%);
      z-index: 100;
      @media (max-width: 1183px) {
         width: 201px;
         height: 201px;
         transform: translate(55%, 46%);
      }
      @media (max-width: 992px){
         transform: translate(92%, 46%);
      }
      @media (max-width: 768px){
         width: 150px;
         height: 150px;
         top: 0%;
         left: 50%;
         transform: translate(-50%, -50%);
      }
      @media (max-width: 450px){
         width: 100px;
         height: 100px;
      }
      &::after {
         content: "";
         position: absolute;
         left: 50%;
         top: 50%;
         transform: translate(-50%, -50%);
         width: 9px;
         height: 9px;
         background: #ffffff;
         border-radius: 50%;
         opacity: 1;
         z-index: 100;
      }
      &::before{
         content: '';
         position: absolute;
         top: 50%;
         left: 50%;
         width: 561px;
         height: 1px;
         opacity: 0.2;
         background: #ffffff;
         @media (max-width: 768px){
            width: 100%;
            left: 0;
         }
      }
   }
}
