.section {
   &__priorities {
      padding: 130px 0 100px;
   }
}
.priorities {
   display: flex;
   align-items: flex-start;
   justify-content: space-between;
   @media (max-width: 992px){
      flex-direction: column;
   }
   &__content {
      max-width: 470px;
      width: 100%;
      margin-right: 130px;
      @media (max-width: 1183px) {
         margin-right: 0px;
         max-width: 400px;
      }
      @media (max-width: 992px){
         margin-bottom: 30px;
         max-width: 500px;
      }
   }
   &__title {
      font-weight: 600 !important;
      color: #222a3c;
      margin-bottom: 50px;
      @media (max-width: 1183px) {
         margin-bottom: 20px;
      }
      span {
         color: #2855a1;
      }
   }
   &__subtitle {
      color: #444a58;
   }
   &__items {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 30px;
      @media (max-width: 600px){
         gap: 15px;
      }
      @media (max-width: 480px){
         grid-template-columns: repeat(1, 1fr);
      }
   }
   &__item {
      width: 100%;
      background: #f8f8f8;
      padding: 65px 35px 35px;
      @media (max-width: 600px){
         padding: 45px 20px 20px;
      }
   }
   &__img {
      position: relative;
      width: 100%;
      height: auto;
      display: flex;
      justify-content: center;
      margin-bottom: 67px;
      img {
         width: 140px;
         height: 140px;
         object-fit: cover;
         border-radius: 50%;
      }
   }
   &__item-number {
      margin-bottom: 0;
      left: 30px;
      position: absolute;
      bottom: 0;
      @media (max-width: 1183px) {
         left: 0;
      }
      @media (max-width: 480px){
         left: 90px;
      }
      @media (max-width: 400px){
         left: 70px;
      }
      @media (max-width: 350px){
         left: 50px;
      }
   }
   &__item-title {
      font-weight: 600 !important;
      color: #444a58;
   }
}
