.section {
   &__new {
      padding: 53px 0 130px;
   }
}
.new {
   &__btn {
      display: flex;
      align-items: center;
      color: #222a3c;
      margin-bottom: 33px;
      &:hover {
         .new__btn-arrow {
            background: #2855a1;
            transition: all 0.3s ease;
         }
      }
   }
   &__btn-arrow {
      width: 30px;
      height: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #394c5b;
      border-radius: 50%;
      margin-right: 20px;
      transition: all 0.3s ease;
      img {
         transform: rotate(90deg);
      }
   }
   &__body {
      display: flex;
      align-items: flex-start;
      position: relative;
      justify-content: space-between;
      margin-bottom: 100px;
      @media (max-width: 768px){
         flex-direction: column;
      }
   }
   &__header {
      min-width: 570px;
      max-width: 570px;
      width: 100%;
      position: sticky;
      left: 0;
      top: 0;
      @media (max-width: 992px){
         max-width: 400px;
         min-width: 400px;
      }
      @media (max-width: 768px){
         position: unset;
         min-width: fit-content;
         max-width: 100%;
         margin-bottom: 40px;
      }
   }
   &__title {
      color: #222a3c;
      margin-bottom: 80px;
      @media (max-width: 992px){
         font-size: 30px;
         margin-bottom: 30px;
      }
      @media (max-width: 768px){
         margin-bottom: 10px;
      }
      @media (max-width: 480px){
         font-size: 24px;
      }
   }
   &__container &__title{
      margin-bottom: 42px;
      @media (max-width: 768px){
         margin-bottom: 22px;
      }
   }
   &__date {
      display: flex;
      align-items: center;
   }
   &__date-number {
      font-weight: 600 !important;
      font-feature-settings: "pnum" on, "lnum" on;
      color: #2855a1;
      margin-right: 30px;
      @media (max-width: 480px){
         margin-right: 20px;
      }
   }
   &__img {
      width: 100%;
      height: auto;
      margin-bottom: 20px;
      img {
         width: 100%;
         height: auto;
      }
   }
   &__text {
      color: #222a3c;
      margin-bottom: 20px;
   }
   &__list {
      margin-left: 20px;
      margin-bottom: 20px;
      li{
         margin-bottom: 0;
      }
   }
}
