footer {
   background-image: url(..//img/footer-bg.webp);
}
.footer {
   display: flex;
   justify-content: space-between;
   align-items: flex-start;
   padding: 70px 0;
   @media (max-width: 768px){
      flex-direction: column;
      align-items: center;
   }
   &__info {
      max-width: 314px;
      width: 100%;
      @media (max-width: 992px){
         max-width: 250px;
      }
      @media (max-width: 768px){
         text-align: center;
         margin-bottom: 25px;
      }
   }
   &__subtext {
      margin-top: 38px;
      color: #ffffff;
      @media (max-width: 992px){
         margin-top: 28px;
      }
      @media (max-width: 768px){
         margin-top: 10px;
      }
   }
   &__menu {
      display: flex;
      align-items: flex-start;
      @media (max-width: 768px){
         flex-direction: column;
         align-items: center;
         margin-bottom: 20px;
      }
   }
   &__list {
      margin-right: 50px;
      list-style: none;
      @media (max-width: 992px){
         margin-right: 30px;
      }
      @media (max-width: 768px){
         margin-right: 0;
         margin-bottom: 10px;
      }
      &:last-child{
         margin-right: 0;
      }
      li{
         margin-bottom: 20px;
         @media (max-width: 768px){
            margin-bottom: 10px;
            text-align: center;
         }
         &:last-child{
            margin-bottom: 0;
         }
      }
   }
   &__text {
      font-weight: 500 !important;
      color: #ffffff;
   }
   &__contact {
      display: flex;
      flex-direction: column;
      @media (max-width: 768px){
         text-align: center;
      }
   }
   &__contact-text {
      color: #9BB8E9;
      margin-top: 30px;
      @media (max-width: 768px){
         margin-top: 20px;
      }
   }
}
