.section {
   &__vacancy {
      background-image: url(..//img/vacancy-bg.webp);
      padding: 100px 0 129px;
      @media (max-width: 768px){
         padding: 50px 0;
      }
   }
}
.vacancy {
   display: flex;
   align-items: center;
   justify-content: space-between;
   @media (max-width: 768px){
      flex-direction: column;
   }  
   &__content {
      max-width: 700px;
      width: 100%;
      @media (max-width: 768px){
         margin-bottom: 40px;
         max-width: 100%;
      }
   }
   &__title {
      color: #ffffff;
      margin-bottom: 33px;
   }
   &__text {
      font-weight: 500 !important;
      color: #ffffff;
   }
   &__btn {
      max-width: 150px;
      width: 100%;
      height: 150px;
      background: #2855a1;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      font-weight: 600;
      text-align: center;
      text-transform: uppercase;
      cursor: pointer;
      transition: all .3s ease;
      &:hover{
         opacity: 0.9;
         transition: all .3s ease;
      }
      @media (max-width: 992px) {
         max-width: 100px;
         height: 100px;
         font-size: 12px;
      }
   }
}
