.section {
   &__hero {
      padding: 250px 0 50px;
      @media (max-width: 1183px) {
         padding: 200px 0 50px;
      }
      @media (max-width: 992px) {
         padding: 150px 0 50px;
      }
      @media (max-width: 768px) {
         padding: 150px 0 20px;
      }
      &-default {
         padding: 58px 0 !important;
         background: linear-gradient(90deg, #233554 50%, #ffffff 50%);
         @media (max-width: 768px) {
            background: #233554;
            position: relative;
            padding: 58px 0 400px !important;
         }
         .hero {
            display: flex;
            align-items: center;
            position: relative;
            justify-content: space-between;
            @media (max-width: 768px) {
               flex-direction: column;
               position: unset;
            }
            &__btn {
               position: absolute;
               bottom: -133px;
               right: -150px;
               max-width: 150px;
               width: 100%;
               height: 150px;
               background: #2855a1;
               border-radius: 50%;
               display: flex;
               justify-content: center;
               align-items: center;
               font-weight: 600;
               text-align: center;
               text-transform: uppercase;
               cursor: pointer;
               transition: all .3s ease;
               &:hover{
                  opacity: 0.9;
                  transition: all .3s ease;
               }
               @media (max-width: 1183px) {
                  right: -50px;
               }
               @media (max-width: 992px) {
                  max-width: 100px;
                  height: 100px;
                  font-size: 12px;
                  bottom: -103px;
                  right: 0;
               }
               @media (max-width: 768px) {
                  bottom: -48px;
               }
            }
            &__content {
               margin-bottom: 0;
               max-width: 50%;
               width: 100%;
               @media (max-width: 768px) {
                  max-width: 100%;
               }
            }
            &__title {
               font-size: 40px;
               margin-bottom: 30px;
               @media (max-width: 992px) {
                  font-size: 32px;
               }
               @media (max-width: 400px){
                  font-size: 26px;
               }
            }
            &__subtitle {
               width: 100%;
            }
            &__img {
               max-width: 40%;
               width: 100%;
               @media (max-width: 768px) {
                  max-width: 100%;
                  position: absolute;
                  bottom: 0;
                  right: 0;
                  left: 0;
                  background: #ffffff;
                  padding: 30px 0 58px;
                  display: flex;
                  justify-content: center;
                  img {
                     max-width: 250px;
                  }
               }
               img {
                  width: 100%;
                  height: auto;
               }
            }
         }
      }
      &-contact {
         background: #233554 !important;
         position: relative;
         padding: 100px 0 138px !important;
         @media (max-width: 768px) {
            padding: 50px 0 300px !important;
         }
         .hero {
            &__content {
               display: flex;
               flex-direction: column;
            }
            &__address {
               margin-top: 30px;
               color: #9bb8e9;
            }
            &__img {
               position: absolute;
               right: 0;
               left: 50%;
               top: -100px;
               bottom: -138px;
               max-width: 100%;
               @media (max-width: 768px) {
                  background: none !important;
                  top: auto;
                  bottom: -57px;
                  left: 0;
                  height: 350px;
                  img {
                     max-width: none;
                  }
               }
               img {
                  width: 100%;
                  height: 100%;
                  object-fit: cover;
                  object-position: center center;
               }
            }
            &__btn {
               bottom: -213px;
               @media (max-width: 992px) {
                  bottom: -188px;
               }
               @media (max-width: 768px) {
                  bottom: -48px;
               }
            }
         }
      }
   }
}
.map {
   width: 100%;
   height: 100vh;
   max-height: 1000px;
   iframe {
      width: 100%;
      height: 100%;
      border: none;
   }
}
.hero {
   &__content {
      margin-bottom: 197px;
      max-width: 626px;
      width: 100%;
      @media (max-width: 992px) {
         margin-bottom: 100px;
      }
   }
   &__title {
      text-transform: uppercase;
      color: #ffffff;
      margin-bottom: 10px;
   }
   &__subtitle {
      font-weight: 500 !important;
      color: #ffffff;
   }
   &__container {
      display: flex;
      align-items: flex-end;
      justify-content: space-between;
      @media (max-width: 768px) {
         flex-direction: column;
         justify-content: center;
         align-items: center;
      }
   }
   &__items {
      display: flex;
      @media (max-width: 768px) {
         width: 100%;
         margin-bottom: 30px;
      }
      @media (max-width: 600px) {
         flex-direction: column;
      }
   }
   &__item {
      margin-right: 30px;
      background: #ffffff;
      position: relative;
      width: 270px;
      z-index: 0;
      &:hover .hero__item-btn{
         transition: all .3s ease;
         transform: translate(5px, 0);
      }
      @media (max-width: 600px) {
         margin-right: 0;
         margin-bottom: 20px;
         width: 100%;
      }
      &:last-child {
         margin-right: 0;
      }
   }
   &__item-title {
      color: #000000;
      margin: 20px 0 64px 20px;
   }
   &__item-btn {
      margin-left: auto;
      margin-bottom: 27px;
      margin-right: 15px;
      width: 30px;
      height: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      background: #394c5b;
      transition: all .3s ease;
      img {
         transform: rotate(-90deg);
      }
   }
   &__item-img {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      z-index: -1;
      img {
         width: 100%;
         height: auto;
      }
   }
   &__scroll {
      width: 50px;
      height: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid #ffffff;
      border-radius: 50%;
      background: radial-gradient(50% 50% at 50% 50%, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.6) 73.96%);
      transition: all .3s ease;
      &:hover{
         background: #2855A1;
         transition: all .3s ease;
      }
   }
}
.modal__wrapper {
   position: fixed;
   left: 0;
   top: 0;
   bottom: 0;
   right: 0;
   width: 100%;
   height: 100%;
   display: none;
   justify-content: center;
   align-items: center;
   background: rgba(0, 0, 0, 0.35);
   .modal {
      width: 450px;
      position: relative;
      padding: 30px;
      background: #ffffff;
      &__close {
         width: 26px;
         height: 20px;
         position: absolute;
         right: 10px;
         top: 10px;
         z-index: 99;
         cursor: pointer;
         &::after,
         &::before {
            content: "";
            position: absolute;
            width: 100%;
            height: 2px;
            left: 0;
            right: 0;
            background-color: #000000;
            transition: 0.3s ease 0s;
            transform-origin: right;
         }
         &::before {
            transform: rotate(-45deg);
            top: 0;
            transition: 0.3s ease 0s;
         }
         &::after {
            transform: rotate(45deg);
            bottom: 0;
            transition: 0.3s ease 0s;
         }
      }
      &__title {
         color: #2855a1;
         margin-bottom: 10px;
      }
      &__text {
         color: #394245;
      }
      &__form{
         margin-top: 20px;
      }
      &__form-input {
         padding: 15px 15px 17px;
         background: #ffffff;
         border: 1px solid #e8e8e8;
         width: 100%;
         margin-bottom: 20px;
         &::placeholder{
            color: #394245;
            opacity: 1;
         }
      }
      &__form-textarea{
         height: 109px;
         resize: none;
         font-family: sans-serif;
      }
      &__form-btn{
         color: #2855A1 !important;
         font-weight: 600;
         padding: 15px 40px;
         border: 1px solid #2855A1;
         margin-top: 10px;
         cursor: pointer;
         background: #ffffff;
         transition: all .3s ease;
         &:hover{
            transition: all .3s ease;
            color: #ffffff !important;
            background: #2855A1;
         }
      }
   }
}
