.section {
   &__services {
   }
}
.services {
   &__swiper {
      height: 1000px;
      background: #222a3c;
      position: relative;
      z-index: 0;
      @media (max-width: 1183px) {
         height: 700px;
      }
      @media (max-width: 768px){
         height: 600px;
      }
      @media (max-width: 400px){
         height: 650px;
      }
      &::after {
         content: url(..//img/services-items-bg.webp);
         position: absolute;
         right: 149px;
         bottom: 0;
         width: 966px;
         height: 864px;
         z-index: -1;
         @media (max-width: 1183px) {
            transform: scale(0.6);
            width: 100%;
            height: 100%;
         }
      }
      .slide {
         background-image: url(..//img/services-items-bg2.webp);
         background-size: 100% 100% !important;
         padding: 40px;
         position: relative;
         z-index: 0;
         @media (max-width: 1183px) {
            padding: 20px;
         }
         &::after {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            right: 0;
            width: 100%;
            height: 65px;
            background: #2855a1;
            z-index: -1;
            opacity: 0;
            transition: all 0.3s ease;
         }
         &:hover {
            &::after {
               opacity: 1;
               transition: all 0.3s ease;
            }
            .slide__number {
               background: #ffffff;
               color: #2855a1;
               transition: all 0.3s ease;
            }
            .slide__btn {
               opacity: 1;
               visibility: visible;
               transition: all 0.3s ease;
            }
         }
         &:first-child {
            .slide__number {
               margin-bottom: 110px;
               @media (max-width: 1183px) {
                  margin-bottom: 50px;
               }
            }
         }
         &__btn {
            padding: 15px 40px;
            border: 1px solid #ffffff;
            font-weight: 600 !important;
            color: #ffffff;
            transition: all 0.3s ease;
            opacity: 0;
            visibility: hidden;
            &:hover {
               background: #ffffff;
               color: #222a3c;
               transition: all 0.3s ease;
            }
         }
         &__content {
            margin-bottom: 94px;
         }
         &__number {
            width: 50px;
            height: 50px;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 50%;
            border: 1px solid #ffffff0c;
            text-transform: uppercase;
            color: #ffffff;
            margin-bottom: 307px;
            @media (max-width: 1183px) {
               margin-bottom: 180px;
            }
            @media (max-width: 992px) {
               margin-bottom: 165px;
            }
            @media (max-width: 768px) {
               margin-bottom: 148px;
            }
         }
         &__title {
            color: #ffffff;
            margin-bottom: 119px;
         }
         &__subtitle {
            color: #ffffff;
            opacity: 0.5;
            margin-bottom: 30px;
            @media (max-width: 992px) {
               font-size: 20px;
               margin-bottom: 20px;
            }
         }
         &__text {
            color: #ffffff;
            margin-bottom: 16px;
            @media (max-width: 768px) {
               font-size: 14px;
               margin-bottom: 14px;
            }
            &:last-child {
               margin-bottom: 0;
            }
         }
      }
      .swiper-scrollbar {
         opacity: 0.3 !important;
         background: #ffffff !important;
         width: 127.5px;
         height: 2px;
         z-index: 99;
         &-drag {
            opacity: 1 !important;
            background: #ffffff !important;
         }
      }
      .swiper__nav {
         display: flex;
         position: absolute;
         bottom: 50px;
         left: 40px;
         align-items: center;
         @media (max-width: 1183px) {
            display: none;
         }
      }
      .scrollbar__text {
         font-weight: 500;
         margin-bottom: 0;
         margin-right: 30px;
      }
   }
}
