*,
*::before,
*::after {
	box-sizing: border-box;
}

html,
body {
	position: relative; 
}

body {
	min-height: 100vh;
	height: 100%;
	display: flex;
	flex-direction: column;
	overflow-x: hidden !important;
	font-style: normal;
	font-family: "Commissioner", sans-serif;
}

main {
	flex: 1 1 auto;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	padding: 0;
}
a {
	text-decoration: none;
}
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
div,
section,
header,
footer {
	background-repeat: no-repeat;
	background-size: cover;
}
:focus-visible {
	outline: none;
}
.container {
	max-width: $mw;
	margin: 0 auto;
	width: 100%;
	@media (max-width: $md1) {
		max-width: 950px;
	}
	@media (max-width: $md2) {
		max-width: 750px;
	}
	@media (max-width: $md3) {
		padding: 0 20px;
		max-width: 100%;
	}
	@media (max-width: 400px) {
		padding: 0 10px;
	}
}
//
.title {
	font-weight: 700;
	font-size: 60px;
	line-height: 1.3;
	@media (max-width: 768px) {
		font-size: 48px;
	}
	@media (max-width: 600px) {
		font-size: 36px;
	}
	@media (max-width: 400px) {
		font-size: 30px;
	}
}
.title-big {
	font-weight: 700;
	font-size: 36px;
	line-height: 1.3;
}
.subtitle {
	font-weight: 600;
	font-size: 24px;
	line-height: 1.5;
}
.subtitle-big {
	font-weight: 500;
	font-size: 18px;
	line-height: 1.5;
}
.subtext {
	font-weight: 400;
	font-size: 16px;
	line-height: 1.5;
}
.text {
	font-weight: 400;
	font-size: 14px;
	line-height: 1.5;
}
.text-date {
	font-weight: 400;
	font-size: 12px;
	line-height: 1.5;
	font-feature-settings: "pnum" on, "lnum" on;
	color: #444a58;
}
